@import url(https://fonts.googleapis.com/css2?family=Sansita+Swashed:wght@300&display=swap);
*{
  box-sizing:border-box;
  padding:0;
  margin:0;
  font-family: 'Sansita Swashed', cursive;

}
.note p{
  font-size:1.1em;
  margin-bottom:10px;
  white-space:pre-wrap;
  word-wrap:break-word;
}

.note h1{
  font-size:1.1em;
  margin-bottom:6px;
}
.note{
  background:white;
  border-radius:7px;
  box-shadow:0  2px 5px red;
  padding:10px;
  width:240px;
margin:16px;
float:left;
 
}

.MuiButton-root:hover .plus_sign{
  color:green;
  background-color: transparent;
}

.MuiButton-root{
  height:50px;
  position:absolute;
  bottom:-28px;
  left:400px;
  cursor:pointer;
  background-color:pink !important;
  box-shadow:5px 5px 15px -5px rgba(0,0,0,1);
  border-radius:50% !important;
}

textarea{
  width:100%;
  line-height:30px;
  background:transparent;
  border:none;
  outline:none;
  font-size:1rem;
  margin:10px 0;

}
input{
  width:100%;
  line-height:30px;
  background:transparent;
  border:none;
  outline:none;
  font-size: 1.5rem;
  margin:10px 0;
  font-weight: bold;
}
form{
  min-height:50px;
  color:grey;
  padding:15px 10px 5px 10px;
  box-shadow:3px 5px 15px -5px rgba(0,0,0,1);
  border-radius: 10px;
  position: relative;
}
.my_notes p{
  color:grey;
  position:fixed;

}
.my_notes h1{
  color:violet;
}

.main_note{
  width:500px;
  padding:15px 10px 5px 10px;
  margin:20px auto;
}
.header{
  width:100%;
  line-height: 90px;
  background-color: orange;
  display:flex;
  padding:50px;
  justify-content: start;
  height:30px;
  align-items:center;
  padding-left: 50px;
  color:cyan;
  box-shadow:5px 3px 15px -5px rgba(0,0,0,0.6)
  
}
h1{
  float:center;
}

.note .btn{
  position:relative;
  float:right;
  margin-right:10px;
  color:blue;
  border:none;
  width:50px;
  height:50px;
  background:transparent;
  cursor:pointer;
  outline:none;
  box-shadow: 3px 3px 15px -10px rgba(0,0,0,0.5);
  border-radius:50%
  
}


.deleteIcon{
  width:0.7em;
  height:0.7em;
}

.note .btn:hover{
  background-color:green;

}
.note .btn:hover .deleteIcon{
  color:red;
}
footer{
  width:100%;
  float:center !important;
   position:absolute;
  bottom:0;
   float:bottom;
   background-color: aqua;
  top:960px;
  margin-left: 490px; 
  height:1px;
  font-family: 'Sansita Swashed', cursive;
} 
.footer .footer_bottom{height:400px;
  text-align:center;
  position:absolute;

}




